<template>
  <div class="slideshow" v-if="daysitemslist[0] && !isShowReconmend">
    <div class="slectnav">
      <van-dropdown-menu>
        <van-dropdown-item v-model="value1" :options="option1" />
        <van-dropdown-item v-model="value2" :options="option2" />
        <van-dropdown-item
          v-model="value3"
          @change="onchangesearch"
          :options="option3"
        />
      </van-dropdown-menu>
    </div>
    <van-list
      v-model="loading"
      :finished="finished"
      finished-text="没有更多了"
      :error.sync="error"
      error-text="请求失败，点击重新加载"
      @load="onLoad"
    >
      <template v-if="daysitemslist[0]">
        <ul class="list" id="list">
          <van-cell v-for="(itemslist, index) in daysitemslist" :key="index">
            <li v-for="(item, index) in itemslist" :key="index">
              <div class="content" :key="index" @click="gocontenttody(item)">
                <img :src="item.image" alt="" />
                <div class="cont">
                  <h4 v-if="item.cates[0]">
                    {{ item.cates[0].catename }}/{{ getmin(item) }}
                  </h4>
                  <h3>{{ item.title }}</h3>
                </div>
              </div>
            </li>
          </van-cell>
        </ul>
      </template>
    </van-list>
    <template> </template>
  </div>
</template>
<script>
// import ItemCard from "@/components/ItemCard";
export default {
  name: "SearchItem",
  props: ["isgetdata", "submitwords", "firstreponse", "isShowReconmend"],
  data() {
    return {
      list: [],
      loading: false,
      lists: [],
      error: false,
      iserror: false,
      finished: false,
      getison: true,
      daysitemslist: [],
      ispush: true,
      isdiffent: false,
      isurl: false,
      value1: 0,
      value2: 0,
      value3: 0,
      option1: [],
      option2: [],
      option3: [{ text: "全部频道", value: 0 }],
      scrollTop: 0
    };
  },
  watch: {
    firstreponse() {
      if (this.firstreponse) {
        this.daysitemslist = [];
        this.lists = [];
        this.lists.push(this.firstreponse.result.next_page_url_full);
        this.daysitemslist.push(this.firstreponse.result.list);
        this.option1 = [];
        this.option2 = [];
        this.option3 = [{ text: "全部频道", value: 0 }];
        this.firstreponse.filter.type.forEach((e, i) => {
          this.option1.push({ text: e.key, value: i, data: e.value });
        });
        this.firstreponse.order.forEach((e, i) => {
          this.option2.push({ text: e.key, value: i, data: e.value });
        });
        this.firstreponse.filter.cate.forEach((e, i) => {
          // window.console.log(e,i)
          this.option3.push({
            text: e.catename,
            value: i + 1,
            cateid: e.cateid
          });
        });
      }
    },
    submitwords() {
      if (this.submitwords) {
        this.getfindheaderdata(this.submitwords);
      }
    }
  },
  computed: {},
  methods: {
    onScrollBottom() {
      this.$nextTick(() => {
        const container = this.$el.querySelector(".list");
        if (container)
          document.documentElement.scrollTop = container.scrollHeight;
        if (container.scrollHeight) {
          this.scrollTop = container.scrollHeight - 500;
        }
      });
    },
    onchangesearch(index) {
      if (index > 0) {
        let text = this.option3[index].text;
        this.$emit("subselactnavtext", text);
      }
    },
    gocontenttody(item) {
      this.$store.commit("submitIsactive", true);
      this.$store.commit("submitLikeItem", item);
      this.$router.push({
        path: "finditemcontent",
        query: { id: item.postid }
      });
    },
    getfindheaderdata(p) {
      if (this.isgetdata) {
        this.axios
          .get("http://api.kele8.cn/agent/" + p)
          .then(reponse => {
            if (this.ispush) {
              this.lists.push(reponse.data.data.result.next_page_url_full);
              this.daysitemslist.push(reponse.data.data.result.list);
              this.finished = true;
              this.ispush = false;
            } else {
              this.daysitemslist.forEach(element => {
                if (element !== reponse.data.data.list) {
                  this.isdiffent = false;
                } else {
                  this.isdiffent = true;
                }
              });
              this.lists.forEach(element => {
                if (element !== reponse.data.data.result.next_page_url_full) {
                  this.isurl = false;
                } else {
                  this.isurl = true;
                }
              });
              if (!this.isdiffent && reponse.data.data && !this.isurl) {
                this.lists.push(reponse.data.data.result.next_page_url_full);
                this.daysitemslist.push(reponse.data.data.result.list);
                this.finished = true;
              }
            }
          })
          .catch(() => {
            this.error = true;
            this.iserror = true;
          });
      }
    },
    onLoad() {
      this.onScrollBottom();

      if (this.iserror) {
        this.getfindheaderdata(this.lists[this.lists.length - 1]);
      }
      this.getfindheaderdata(this.lists[this.lists.length - 1]);
      // 异步更新数据
      setTimeout(() => {
        // 加载状态结束
        this.loading = false;
        // 数据全部加载完成
        // if (this.list.length <= this.lists.length) {
        //   this.finished = true;
        // }
        this.finished = false;
      }, 500);
    },
    getmin(i) {
      let time = i.duration;
      let n = Math.floor(time / 60);
      let s = Math.floor(time % 60);
      if (n < 10) {
        n = "0" + n;
      }
      if (s < 10) {
        // window.console.log("0" + s);
        s = "0" + s;
      }
      let m = n + "'" + s + '"';
      return m;
    },
    getbodyheight() {
      let body = document.querySelector(body);
      window.console.log(body);
    }
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      let c = vm.$el;
      window.console.log(c, vm._data.scrollTop);
      if (vm._data.scrollTop) {
        c.scrollTop = vm._data.scrollTop;
      }
    });
  }
};
</script>

<style lang="less">
.slideshow {
  flex: 1;
  height: 100%;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  .slectnav {
    .van-dropdown-menu {
      background: rgb(27, 27, 27);
      height: 35px;
      position: static;
      border-top: 1px solid rgb(41, 40, 40);
      .van-dropdown-menu__item {
        .van-dropdown-menu__title {
          color: rgb(180, 175, 175);
          // &::after {
          //   // color: white;
          // }
          // .van-ellipsis {
          //   // color: white;
          // }
        }
      }
      .van-dropdown-item {
        .van-popup {
          background: none;
          .van-cell {
            background: rgb(27, 27, 27);
            border-top: 1px solid rgb(31, 30, 30);
            &::after {
              position: static;
            }
            .van-cell__title {
              color: rgb(180, 175, 175);
              font-size: 10px;
            }
          }
        }
      }
      div {
        &:nth-of-type(6) {
          .van-dropdown-item {
            width: 100%;
            height: 100vh;
            background: #1b1b1b;
            .van-overlay {
              background: none;
            }
            .van-popup {
              width: 100%;
              display: flex;
              flex-wrap: wrap;
              .van-cell {
                padding: 5px;
                width: auto;
                .van-cell__value {
                  display: none;
                }
                .van-cell__title {
                  span {
                    background: #242422;
                    padding: 4px 8px;
                    border-radius: 3px;
                  }
                  .active {
                    border: 1px solid white;
                  }
                }
              }
            }
          }
        }
      }
    }
  }
  #list {
    .van-cell {
      top: 0;
      padding: 0;
      // background: rgb(180, 175, 175);
      &::after {
        position: static;
      }
    }
  }
  .van-list__error-text {
    line-height: 32px;
    text-align: center;
    background: #161616;
    color: white;
    height: 5vh;
  }
  .van-list {
    flex: 1;
    overflow-y: scroll;
  }
  .list {
    li {
      h1 {
        text-align: center;
        background: #161616;
        color: white;
        font-size: 1em;
        padding: 15px 0;
        letter-spacing: 2px;
      }
      .content {
        position: relative;
        height: 34vh;
        vertical-align: middle;
        img {
          width: 100%;
          height: 100%;
          vertical-align: middle;
        }
        .cont {
          color: white;
          position: absolute;
          bottom: 15px;
          left: 15px;
          h4 {
            font-size: 10px;
            line-height: 28px;
            font-weight: 200;
            span {
              padding: 0px 5px;
            }
          }
          h3 {
            font-size: 14px;
          }
        }
      }
      .contents {
        color: red;
        display: flex;
        flex-wrap: wrap;
        .icon {
          width: 50%;
          height: 50%;
          position: relative;
          img {
            width: 100%;
            height: 100%;
          }
          .cont {
            color: white;
            position: absolute;
            bottom: 5px;
            left: 15px;
            h4 {
              font-size: 10px;
              line-height: 20px;
              font-weight: 200;
              span {
                padding: 0px 3px;
              }
            }
            h3 {
              font-size: 13px;
              display: -webkit-box;
              -webkit-line-clamp: 1;
              -webkit-box-orient: vertical;
              overflow: hidden;
            }
          }
          &:nth-child(1) {
            display: none;
          }
        }
      }
    }
  }
}
</style>
