r<template>
  <div class="search">
    <form action="/">
      <van-search
        v-model="value"
        placeholder="请输入要搜索的内容"
        show-action
        @input="onInput"
        @cancel="OnCancel"
        @search="onSearch"
        background="rgb(27, 27, 27)"
      />
    </form>
    <div class="searchnav" v-if="isShowReconmend">
      <div class="recommendwords">
        <h4>热门搜索</h4>
        <van-tag
          text-color="white"
          color="#242422"
          size="medium"
          type="danger"
          v-for="(item, index) in recommendWords"
          :key="index"
          @click="goSearchContent(item.kw)"
          ><span>{{ item.kw }}</span></van-tag
        >
      </div>
      <div class="searchhistory">
        <div class="searchtite">
          <span>搜索历史</span
          ><span @click="showDelate">清除<van-icon name="delete"/></span>
        </div>
        <van-tag
          style="border-radius: 3px;"
          text-color="white"
          color="#242422"
          size="medium"
          v-for="(item, index) in searchhistory"
          :key="index"
          @click="goSearchContent(item)"
          ><span>{{ item }}</span></van-tag
        >
      </div>
      <van-overlay :show="showdelate" @click="showdelate = false">
        <div class="wrapper" @click.stop>
          <h3>清空搜索记录?</h3>
          <div class="ok">
            <span @click="showdelate = false">取消</span>
            <span @click="OkDElateSearchHistory">确认</span>
          </div>
        </div>
      </van-overlay>
    </div>
    <SearchItem
      :submitwords="submitwords"
      :isgetdata="isgetdata"
      :firstreponse="firstreponse"
      :isShowReconmend="isShowReconmend"
      :recommendwordisalter="recommendwordisalter"
      @subselactnavtext="subselactnavtext($event)"
    />
    <!-- <keep-alive> -->
    <!-- <router-view></router-view> -->
    <!-- </keep-alive> -->
  </div>
</template>

<script>
import SearchItem from "@/components/SearchItem";
// http://api.kele8.cn/agent/https://app.vmovier.com/apiv3/search?kw=
export default {
  name: "Search",
  components: {
    SearchItem
  },
  data() {
    return {
      showdelate: false,
      value: null,
      recommendWords: null,
      searchhistory: null,
      isgetdata: false,
      submitwords: null,
      firstreponse: null,
      firstrequestdata: true,
      isShowReconmend: true,
      recommendwordisalter: true
    };
  },
  watch: {
    firstreponse() {
      if (this.firstreponse) {
        this.isShowReconmend = false;
      }
    },
    value() {
      if (!this.value) {
        this.isShowReconmend = true;
        this.firstreponse = null;
      }
    }
  },
  methods: {
    onInput() {},
    OnCancel() {
      // window.console.log("xx");
      this.$router.go(-1);
    },
    onSearch(value) {
      this.submitwords = value;
      this.getSearchitem(value);
      let arr = [];
      arr.unshift(value);
      let searchhistory = JSON.parse(
        window.localStorage.getItem("searchhistory")
      );
      this.searchhistory = searchhistory;
      //第一次存
      if (!searchhistory) {
        window.localStorage.setItem("searchhistory", JSON.stringify(arr));
      } else {
        window.console.log(searchhistory.includes(value));
        if (!searchhistory.includes(value)) {
          searchhistory.unshift(value);
          window.localStorage.setItem(
            "searchhistory",
            JSON.stringify(searchhistory)
          );
        }
      }
    },
    subselactnavtext(text) {
      // window.console.log(text);
      this.getSearchitem(text);
      this.value = text;
    },
    getSearchitem(x) {
      if (this.firstrequestdata) {
        this.axios
          .get(
            "https://api.kele8.cn/agent/https://app.vmovier.com/apiv3/search?kw=" +
              x
          )
          .then(reponse => {
            // window.console.log(reponse);
            this.firstreponse = reponse.data.data;
            // this.firsturl = reponse.data.data.result.next_page_url_full;
            // this.daysitemslistfirst = reponse.data.data.result.list;
            // this.finditemsdata = reponse.data.data;
            // this.$store.commit("submitcardllist", reponse.data.data);
            // ({ playlist: this.playlists } = reponse.data);
            // window.localStorage.setItem(
            //   "finditemsdata",
            //   JSON.stringify({
            //     time: Date.now() + 60 * 60 * 1000,
            //     data: this.finditemsdata
            //   })
            // );
          });
      }
    },
    goSearchContent(x) {
      this.value = x;
      this.submitwords = x;
      // window.console.log(x);
      this.getSearchitem(x);
    },
    showDelate() {
      this.showdelate = true;
    },
    //确定清空搜索记录
    OkDElateSearchHistory() {
      this.showdelate = false;
      let n = JSON.parse(window.localStorage.getItem("searchhistory"));
      n = [];
      window.console.log(n);
      window.localStorage.setItem("searchhistory", JSON.stringify(n));
    },
    getSearchHistory() {
      let a = JSON.parse(window.localStorage.getItem("searchhistory"));
      if (a) {
        window.console.log(a);
        this.searchhistory = a.slice(0, 10);
      }
    },
    getDayCover() {
      this.axios
        .get(
          " https://api.kele8.cn/agent/https://app.vmovier.com/apiv3/search?kw="
        )
        .then(response => {
          this.recommendWords = response.data.data.recommend_keywords;
          // window.console.log(response.data.data.recommend_keywords);
        });
    }
  },
  created() {
    this.getDayCover();
    this.getSearchHistory();
    this.isgetdata = true;
  }
};
</script>

<style lang="less">
.search {
  overflow: hidden;
  position: relative;
  vertical-align: middle;
  background: rgb(27, 27, 27);
  height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  .van-overlay {
    vertical-align: middle;
    display: flex;
    justify-content: center;
    align-items: center;
    .wrapper {
      background: white;
      height: 15vh;
      width: 80%;
      padding: 15px;
      border-radius: 4px;
      h3 {
        font-size: 14px;
      }
      .ok {
        line-height: 4.5em;
        font-size: 12px;
        color: lightseagreen;
        text-align: right;
        span {
          padding: 0px 15px;
        }
      }
    }
  }
  .van-search {
    // position: fixed;
    // width: 100%;
    z-index: 3;
    background: rgb(27, 27, 27);
    padding-top: 25px;
    padding-right: 5px;
    padding-bottom: 20px;
    .van-search__action {
      color: rgb(180, 175, 175);
      &:active {
        background: rgb(27, 27, 27);
      }
    }
    .van-search__content {
      background: #242422;
    }
    .van-cell {
      input {
        color: white;
      }
      .van-icon {
        color: rgb(180, 175, 175);
      }
      .van-search__action {
        .active {
          background: none;
        }
      }
    }
  }
  .searchnav {
    .recommendwords {
      padding: 10px;
      h4 {
        padding: 5px;
        color: rgb(180, 175, 175);
        font-size: 13px;
      }
      span.van-tag {
        // padding: 0 5px;
        // background: rgb(27, 27, 27);
        margin: 3px 5px;
        font-size: 1px;
        span {
          font-size: 10px;
          padding: 3px 5px;
        }
      }
    }
    .searchhistory {
      padding: 10px;
      .searchtite {
        padding: 5px;
        color: rgb(180, 175, 175);
        font-size: 13px;
        &::after {
          content: "";
          display: block;
          clear: both;
        }
        span {
          line-height: 26px;
          &:first-child {
            float: left;
          }
          &:last-child {
            float: right;
            display: flex;
            align-items: center;
            i {
              padding-top: 2px;
            }
          }
        }
      }
      .van-tag {
        // padding: 0 5px;
        margin: 3px 5px;
        font-size: 1px;
        span {
          font-size: 10px;
          padding: 3px 5px;
        }
      }
    }
  }
}
</style>
